  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 10px 0 5px 0;
}
.cookie_consent_container .cookie_consent:after {
  display: table;
  content: "";
  clear: both;
}
.cookie_consent_container .cookie_consent p {
  margin: 0;
}
.cookie_consent_container .cookie_consent .cookie_consent_left {
  color: #b81f5f;
  width: 100%;
}
.cookie_consent_container .cookie_consent .cookie_consent_left .cookie_consent_title {
  font-size: 1.25rem;
  line-height: 1;
  margin-bottom: 0.5rem;
}
.cookie_consent_container .cookie_consent .cookie_consent_left .cookie_consent_text {
  font-size: 1rem;
  line-height: 1;
  margin-bottom: 0.5rem;
}
.cookie_consent_container .cookie_consent .cookie_consent_right {
  width: 100%;
  margin-top: 10px;
}
.cookie_consent_container .cookie_consent .cookie_consent_right .cookie_consent_buttons .button {
  font-size: 1rem;
  margin: 0;
}

.cookie_button {
  font-size: 1.25rem;
  color: #fefefe;
  font-weight: 700;
  background: #b81f5f;
  position: fixed;
  top: 0;
  right: 0;
  text-align: center;
  padding: 4px 15px 6px 15px;
